@import '../../styles/variables';

.wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 700px;
    gap: 20px;
    & > p {
        margin: 0px;
    }
}

.link {
    color: hsl(206, 100%, 52%);
    text-decoration: none;
    &:hover {
        text-decoration: underline;
    }
}
